@import url("./global.scss");

container {
  width: 80vw;
  margin-left: 10vw;
  display:flex;
  align-items: center;
  height:calc(100vh - 60px);

wrap {
  display: block;
  margin:0;
  padding:0;
  z-index:1;
}

img {
  position: absolute;
  left:55vw;
  z-index:0;
  width: 30vw;
}

.typewriter h1 {
  display:inline-block;
  overflow-wrap: break-word;
}


span {
  color: var(--accent);
  transition: 1s ease;
}

span:hover {
  color: var(--accent-dark);
}

.welcome {
  color: var(--accent);
  font-family: var(--mono);
}

button {
  padding:1.5vw;
  background: var(--dark);
  color: var(--accent);
  border-radius: 0.5vw;
  font-weight:400;
  font-family: var(--mono);
  border:2px solid var(--accent);
  margin-top:2vw;
  transition: 1s ease;
}

button:hover {
  box-shadow: 5px 5px;
}
}